aside, details, main, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

audio, canvas, iframe, img, svg, video {
    vertical-align: middle;
}

fieldset {
    border: none;
    margin: 0;
    padding: 0;
}

summary {
    display: list-item;
}

legend {
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}

progress {
    vertical-align: baseline;
}

*, *:before, *:after {
    margin: 0;
    padding: 0;
    @include prefix(box-sizing, border-box);
}

ul, menu {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    width: 100%;
    height: 100vh;
    min-width: 320px;
    margin: 0;
    padding: 0;
    display: flex;
    position: relative;
    background: $mainBg;
    color: $mainFg;
    font: normal 16px $Nunito;
    flex-direction: column;
    overscroll-behavior: none;
    -webkit-text-size-adjust: 100%;
    overflow-x: hidden;
}
